// components/UpcomingEvents.tsx
import { useState } from 'react';
import { motion } from 'framer-motion';

type EventType = {
    id: string;
    date: string;
    month: string;
    title: string;
    department: string;
    type: string;
    details: string[];
    university: string;
    prices?: number[];
    isSpecial?: boolean;
};

const EventCard = ({ event }: { event: EventType }) => (
    <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        className="bg-white rounded-lg shadow-md overflow-hidden border border-gray-100 hover:shadow-lg transition-shadow"
    >
        {/* رأس البطاقة */}
        <div className="p-4 border-b border-gray-100">
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-2">
                    <span className="text-2xl font-bold text-gray-800">{event.date}</span>
                    <span className="text-sm font-medium text-gray-500 uppercase">{event.month}</span>
                </div>
                {event.isSpecial && (
                    <span className="bg-yellow-100 text-yellow-800 text-xs px-2 py-1 rounded-full">
                        REGISTRATIONS ARE NOW OPEN
                    </span>
                )}
            </div>
        </div>

        {/* محتوى البطاقة */}
        <div className="p-4">
            <h3 className="text-lg font-bold text-gray-800 mb-1">{event.title}</h3>
            <p className="text-sm text-gray-500 mb-3">By {event.department}</p>

            <div className="space-y-2 mb-4">
                {event.details.map((detail, i) => (
                    <div key={i} className="flex items-start">
                        <span className="text-gray-500 mr-2">»</span>
                        <span className="text-sm text-gray-700">{detail}</span>
                    </div>
                ))}
            </div>

            {event.prices && (
                <div className="flex gap-2 mb-4">
                    {event.prices.map((price, i) => (
                        <span key={i} className="bg-gray-100 text-gray-800 px-2 py-1 rounded text-sm">
                            {price.toFixed(2)}
                        </span>
                    ))}
                </div>
            )}

            <p className="text-xs text-gray-500">{event.university}</p>
        </div>
    </motion.div>
);

export default function UpcomingEvents() {
    const [activeFilter, setActiveFilter] = useState<'all' | 'weekdays' | 'popular' | 'latest'>('all');

    // بيانات الأحداث
    const events: EventType[] = [
        {
            id: '1',
            date: '11',
            month: 'MAY',
            title: 'Civil Padura',
            department: 'Civil Engineering Department',
            type: 'Musical',
            details: [
                'Musical Event',
                'All universities students can join'
            ],
            university: 'University of Moratuwa',
            prices: [5.00, 4.20, 8.40, 9.60, 10.00]
        },
        {
            id: '2',
            date: '14',
            month: 'MAY',
            title: 'Yaye Padura',
            department: 'Chemical Department',
            type: 'Musical',
            details: [
                'Musical Event',
                'All universities students can join'
            ],
            university: 'University of Moratuwa'
        },
        {
            id: '3',
            date: '25',
            month: 'APR',
            title: 'Spandana',
            department: 'Medical Faculty',
            type: 'Musical',
            details: [
                'Musical Event',
                'All universities students can join'
            ],
            university: 'University of Moratuwa'
        },
        {
            id: '4',
            date: '28',
            month: 'APR',
            title: 'Devthon',
            department: 'Leo Club UOM',
            type: 'Competition',
            details: [
                'Web designing competition',
                'All UOM students can join'
            ],
            university: 'University of Moratuwa',
            isSpecial: true
        },
        {
            id: '5',
            date: '30',
            month: 'APR',
            title: 'SLIOT',
            department: 'CSE Department',
            type: 'Competition',
            details: [
                'New Innovation Competition',
                'All UOM students can join'
            ],
            university: 'University of Moratuwa'
        },
        {
            id: '6',
            date: '02',
            month: 'MAY',
            title: 'CSE 40',
            department: 'CSE Department',
            type: 'Competition',
            details: [
                'Coding Competition',
                'All UOM students can join'
            ],
            university: 'University of Moratuwa'
        }
    ];

    // فلترة الأحداث حسب النوع المحدد
    const filteredEvents = events.filter(event => {
        if (activeFilter === 'all') return true;
        if (activeFilter === 'weekdays') return ['1', '2', '6'].includes(event.id);
        if (activeFilter === 'popular') return ['1', '4'].includes(event.id);
        if (activeFilter === 'latest') return ['3', '5'].includes(event.id);
        return true;
    });

    return (
        <section className="py-12 bg-gray-50">
            <div className="container mx-auto px-4">
                {/* العنوان وأزرار الفلترة */}
                <div className="flex flex-col md:flex-row justify-between items-center mb-8">
                    <h2 className="text-2xl font-bold text-gray-800 mb-4 md:mb-0">Upcoming Events</h2>
                    <div className="flex gap-2">
                        {['Weekdays', 'Popular', 'Latest'].map((filter) => {
                            const filterKey = filter.toLowerCase() as 'weekdays' | 'popular' | 'latest';
                            return (
                                <motion.button
                                    key={filter}
                                    whileHover={{ scale: 1.05 }}
                                    whileTap={{ scale: 0.95 }}
                                    onClick={() => setActiveFilter(filterKey)}
                                    className={`px-4 py-2 rounded-full text-sm font-medium ${
                                        activeFilter === filterKey
                                        ? 'bg-purple-600 text-white'
                                        : 'bg-white text-gray-600 border border-gray-200'
                                    }`}
                >
                            { filter }
                </motion.button>
                    );
            })}
                </div>
            </div>

            {/* شبكة البطاقات */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
                {filteredEvents.map((event) => (
                    <EventCard key={event.id} event={event} />
                ))}
            </div>

            {/* زر عرض الكل */}
            <div className="text-center">
                <motion.button
                    whileHover={{ scale: 1.05 }}
                    whileTap={{ scale: 0.95 }}
                    className="bg-purple-600 text-white px-6 py-3 rounded-full text-sm font-medium"
                >
                    See All Events
                </motion.button>
            </div>
        </div>
    </section >
  );
}